*{
  list-style: none;
  padding: 0px; margin: 0px;
}
h2{
  text-align: center;
}
.BOX1{
  display: flex;
  width: 1000px;
  margin: 0px auto;
}
li{
  width: 25%;
  margin: 0px 30px;
  cursor: pointer;

}
li>p>img:hover{
  transform: scale(1.1);
  
}
li>p>img{
  width: 100%;
  transition: 0.5s;
  

}
li>p{
  text-align: left;
  width: 100%;
  margin: 20px 0px;
  overflow: hidden;
}
.BOX1>li>p:nth-of-type(2){
  font-weight: bold;
  font-size: 18px;
}
.BOX1>li>p:nth-of-type(3){
  color: red;
  font-weight: bold;
  font-size: 16px;
}
.BOX1>li>p:last-of-type{
  /* color: red; */
  /* font-weight: bold; */
  font-size: 12px;
  color: gray;
}