@charset "utf-8";

form{
  width: 340px;
  margin: 0 auto;
  text-align: center;
}
input{
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
  box-sizing: border-box;
}
ul{
  list-style: none;
  display: flex;
  /* justify-content: flex-end; */
  gap: 5px;
  font-size: 12px;
}
ul li{cursor: pointer;}
input[type=submit]{
  color: #fff;
  border: 1px solid #5f0080;
  background: #5f0080;
  margin-bottom: 10px;
  cursor: pointer;
  border-radius: 3px;
}
input[type=button]{
  color: #5f0080;
  border: 1px solid #5f0080;
  background: #fff;
  cursor: pointer;
  border-radius: 3px;
}
.btn{
  padding: 8px 15px;
  color: #fff;
  border: 1px solid #fff;
  background: #5f0080;
  border-radius: 5px;
  cursor: pointer;
}